*{
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  width: 100%;
  background-color: #1C1E22;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}



header{
	background-color: #111111;
	color:#fff;
	display: flex;
	justify-content: center;
	align-items: center;
	
}

header h1{
	font-weight: 500;
	font-size: 24px;
	text-transform: uppercase;
}

header img{
	width:60px;
}
.aside{
	border-right: 1px solid grey;	
	background-color: #1A1D23;
	color:#fff;
	height: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	
}
.rangeList{
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: center;	
	margin-bottom: 30px;
}

.default{	
	background-color: transparent;
	color:grey;
	border:none;
	outline: none;
	margin: 25px 0;
}

.default:hover{
	cursor: pointer;
	color:rgba(255, 67, 95, 0.93);
}
.rangeWrapper{
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-bottom: 15px;
}

.rangeWrapper label{
	display: flex;
	justify-content: space-between;
	align-items: center;
	width: 100%;
	margin-bottom: 20px;
}


.rangeWrapper h3{
	text-transform: capitalize;
}

.filterValue{
	width: 50px;
	padding: 3px 0;
	background-color: transparent;
	border:1px solid white;
	border-radius: 3px;
	text-align: center;
	color:white;
}





input[type=range] {
	-webkit-appearance: none;
	width:28vw;
	/* margin: 5.25px 0; */
 }
 input[type=range]:focus {
	outline: none;
 }
 input[type=range]::-webkit-slider-runnable-track {
	width: 100%;
	height: 1.5px;
	cursor: pointer;
	box-shadow: 0px 0px 0.5px #000000, 0px 0px 0px #0d0d0d;
	background: #4c4d4d;
	border-radius: 25px;
	border: 0px solid #000101;
 }
 input[type=range]::-webkit-slider-thumb {
	box-shadow: 0px 0px 1px #ff0000, 0px 0px 0px #ff1a1a;
	border: 0px solid #fffa00;
	height: 12px;
	width: 13px;
	border-radius: 30px;
	background: rgba(255, 67, 95, 0.93);
	cursor: pointer;
	-webkit-appearance: none;
	margin-top: -5.25px;
 }
 input[type=range]:focus::-webkit-slider-runnable-track {
	background: #595a5a;
 }
 input[type=range]::-moz-range-track {
	width: 100%;
	height: 1.5px;
	cursor: pointer;
	box-shadow: 0px 0px 0.5px #000000, 0px 0px 0px #0d0d0d;
	background: #4c4d4d;
	border-radius: 25px;
	border: 0px solid #000101;
 }
 input[type=range]::-moz-range-thumb {
	box-shadow: 0px 0px 1px #ff0000, 0px 0px 0px #ff1a1a;
	border: 0px solid #fffa00;
	height: 12px;
	width: 13px;
	border-radius: 30px;
	background: rgba(255, 67, 95, 0.93);
	cursor: pointer;
 }
 input[type=range]::-ms-track {
	width: 100%;
	height: 1.5px;
	cursor: pointer;
	background: transparent;
	border-color: transparent;
	color: transparent;
 }
 input[type=range]::-ms-fill-lower {
	background: #3f4040;
	border: 0px solid #000101;
	border-radius: 50px;
	box-shadow: 0px 0px 0.5px #000000, 0px 0px 0px #0d0d0d;
 }
 input[type=range]::-ms-fill-upper {
	background: #4c4d4d;
	border: 0px solid #000101;
	border-radius: 50px;
	box-shadow: 0px 0px 0.5px #000000, 0px 0px 0px #0d0d0d;
 }
 input[type=range]::-ms-thumb {
	box-shadow: 0px 0px 1px #ff0000, 0px 0px 0px #ff1a1a;
	border: 0px solid #fffa00;
	height: 12px;
	width: 13px;
	border-radius: 30px;
	background: rgba(255, 67, 95, 0.93);
	cursor: pointer;
	height: 1.5px;
 }
 input[type=range]:focus::-ms-fill-lower {
	background: #4c4d4d;
 }
 input[type=range]:focus::-ms-fill-upper {
	background: #595a5a;
 }
 
.filterList{
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: center;
}
.filter{
	margin-bottom: 20px;
	width: 100%;
}

.filter img{
	max-width:100%;
	-webkit-transition:all .3s;
	transition:all .3s;
}

.filter h4{
	margin-top: 10px;
	-webkit-transition:all .3s;
	transition:all .3s;
	text-align: center;
}

.filter:hover > img{
	box-shadow: 0px 0px 20px -3px rgba(0,0,0,0.75);
	cursor: pointer;
}

.filter:hover > h4{
	color:rgba(255, 67, 95, 0.93);
	cursor: pointer;
}

.filter:hover{
	cursor: pointer;
}
.menu{
	margin-top: 30px;
	height: 35px;
	width: 175px;
	display: flex;
	
	border: 1px solid rgba(255, 255, 255, .1);
	border-radius: 5px;
}

.menu__link{
	text-decoration: none;
	text-align: center;
	color:#fff;
	background-color: #1A1D23;
	width: 50%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	-webkit-transition: all .3s;
	transition: all .3s;
}

.menu__link:hover{
	color:rgba(255, 67, 95, 0.93);
}

.menu__link-active{
	background-color: rgb(21, 23, 26);
}


.layout{
	width: 100%;
}
.main{
	display: grid;
	min-height: 100vh;
}


.imageContainer{
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: center;
	position: relative;
	padding:0 15px;
	min-height: 200px;
}


.InputWrapper{
	display: flex;
	justify-content: space-between;
	width: 75%;
	margin-bottom: 15px;
	height: 35px;
	margin-top: 30px;
}

.Input{
	box-sizing: border-box;
	height: 35px;
	background-color: transparent;
	border-radius: 5px;
	border:none;	
	color:rgba(255,255,255,.3);
	background-color: #111111;
	background-color: #15171A;
	padding: 5px 10px;
}

.Input:focus{
	outline: none;
}

.Button {
	color: #fff;
	text-decoration:none;
	height:35px;
	background-image: -webkit-linear-gradient(left, #00C0FF 0%, #FFCF00 49%, #F2435D 80%, #00C0FF 100%);
	background-image: linear-gradient(90deg, #00C0FF 0%, #FFCF00 49%, #F2435D 80%, #00C0FF 100%);
	border:none;
	border-radius:5px;
	display:flex;
	align-items:center;
	justify-content:center;
	margin-left: 15px;
	outline:none;
	-webkit-transition: all .4s;
	transition: all .4s;
 }

 .Button:after {
	height:30px;
	font-size: 14px;
	background-color:#1C1E22;
	display:flex;
	align-items:center;
	justify-content:center;
 }

 .Button:hover {
	-webkit-animation:slidebg 2s linear infinite;
	        animation:slidebg 2s linear infinite;
	cursor: pointer;
	color:#f2435d;
 }

 .Button:hover:after{
	background-color: #15171A;
 }
 
 @-webkit-keyframes slidebg {
	to {
	  background-position:20vw;
	}
 }
 
 @keyframes slidebg {
	to {
	  background-position:20vw;
	}
 }
 #preloader {
	width: 100px;
	height: 100px;
	position: absolute;
	left:calc(50% - 50px);
	top:calc(50% - 50px);
} 
#loader {
	display: block;
	position: relative;
	left: 50%;
	top: 50%;
	width: 150px;
	height: 150px;
	margin: -75px 0 0 -75px;
	border-radius: 50%;
	border: 3px solid transparent;
	border-top-color: #e77f8f;
	-webkit-animation: spin 2s linear infinite;
	animation: spin 2s linear infinite;
}
#loader:before {
	content: "";
	position: absolute;
	top: 5px;
	left: 5px;
	right: 5px;
	bottom: 5px;
	border-radius: 50%;
	border: 3px solid transparent;
	border-top-color: #cf5668;
	-webkit-animation: spin 3s linear infinite;
	animation: spin 3s linear infinite;
}
#loader:after {
	content: "";
	position: absolute;
	top: 15px;
	left: 15px;
	right: 15px;
	bottom: 15px;
	border-radius: 50%;
	border: 3px solid transparent;
	border-top-color: #f2435d;
	-webkit-animation: spin 1.5s linear infinite;
	animation: spin 1.5s linear infinite;
}
@-webkit-keyframes spin {
	0%   {
		 -webkit-transform: rotate(0deg);
		 transform: rotate(0deg);
	}
	100% {
		 -webkit-transform: rotate(360deg);
		 transform: rotate(360deg);
	}
}
@keyframes spin {
	0%   {
		 -webkit-transform: rotate(0deg);
		 transform: rotate(0deg);
	}
	100% {
		 -webkit-transform: rotate(360deg);
		 transform: rotate(360deg);
	}
}
.Img{
	margin-bottom: 10px;
}

.imgMain{
	max-width: 100%;
	-webkit-transition: all .4s;
	transition: all .4s;
	box-shadow: 0px 0px 17px -3px rgba(0,0,0,0.75);
