@media  screen and (min-width: 1171px){
	.main{
		grid-template-columns: 300px 5fr;
	}

	.aside{
		padding:0 40px;
	}

	.Img{
		max-width: 74%;
	}

	input[type=range] {
		-webkit-appearance: none;
		width:225px;
	 }

	.Input{
		width: 85%;
	}

	.Button {
		width:calc(14% + 4px);
	 }
	 
	.Button:after {
		content:attr(alt);
			width:95%;
	}


}

